/* PrisonDilemmaGame.css */

.game-container {
    position: relative;
    /* Set the container as a positioning context */
    display: grid;
    grid-template-areas:
        "title"
        "pop-up"
        "buttons"
        "points"
        "prompt";
    gap: 10px;
    margin: 20px;
    height: 100vh;
    /* Set height to 100% of the viewport */
}

.pop-up-container {
    grid-area: pop-up;
    display: flex;
    justify-content: center;
    align-items: center;
}

.points-container {
    grid-area: points;
    text-align: center;
}

.buttons-container {
    grid-area: buttons;
    display: flex;
    justify-content: space-around;
    margin-top: 20px;
    /* Add margin at the top */
}

.prompt-container {
    grid-area: prompt;
    text-align: center;
}

.popup {
    background-color: #ffffff;
    padding: 20px;
    /* Enlarge the pop-up */
    border: 1px solid #ccc;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    z-index: 999;
}

.game-over {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #ffffff;
    padding: 20px;
    border: 1px solid #ccc;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    z-index: 1000;
}

.play-again-button {
    margin-top: 10px;
}